import { fetchJsonp, fetchGet } from '@service/index';
import axios from 'axios';
import getSenseKey from 'lagou-base-captcha/dist/switchCaptchaSense';
import { API_BASE_URL } from '@js/util/api-config.js';

export const fetchPhoneCode = (params) => {
    return fetchJsonp(`https://passport.lagou.com/login/sendVerifyCode.json`, params);
};

export const submitPassWord = (params) => {
    return fetchJsonp(`https://passport.lagou.com/login/registerAndLogin.json`, params);
};

export const fetchPassWordLogin = (params) => {
    return fetchJsonp(`https://passport.lagou.com/login/login.json`, params);
};

export const fetchCountryCode = (params) => {
    return fetchJsonp(`https://passport.lagou.com/register/getPhoneCountryCode.json`, params);
};

export const fetchGetSenseKey = () => {
    return new Promise((resolve, reject) => {
        getSenseKey({
            id: '66442f2f720bfc86799932d8ad2eb6c7',
            successCallback: (item) => {
                resolve(item);
            },
            errorCallback: (error) => {
                reject(error);
            }
        });
    });
};

