import { captchaGetSenseKey } from 'lagou-base-captcha';
import { fetchPhoneCode, submitPassWord } from '@service/login';
import { validateCodeNumber, validatePhoneNumber } from '@js/util/util';
import { getParam } from '@js/util/getParam';
export default {
    data() {
        return {
            phoneValue: '',
            codeValue: '',
            showClearInput: false,
            countryCode: '0086',
            isLoginSuccess: false,
            challenge: '',
            errorTips: {
                show: false,
                text: ''
            },
            phoneCodeStateList: {
                1: { message: '验证码已发送，请查收短信' },
                201: { message: '请输入手机号码' },
                203: { message: '输入号码与归属地不匹配' },
                204: { message: '系统错误' },
                205: { message: '输入号码与归属地不匹配' },
                206: { message: '系统错误' },
                207: { message: '验证码达到发送上限（5次），无法继续发送' },
                208: { message: '验证码发送太过频繁，请稍后再试' },
                209: { message: '该手机已被注册，请重新输入或直接登录' },
                210: { message: '请输入有效的手机/邮箱' },
                211: { message: '请输入6-16位密码，字母区分大小写' },
                220: { message: '请输入已验证手机/邮箱' },
                222: { message: '该手机号未注册' },
                241: { message: '请输入密码' },
                402: { message: '获取验证码超时，请稍后再试' },
                400: { message: '验证码错误' },
                10010: { message: '图形验证码不正确' },
                10011: { message: '操作过于频繁，请联系管理员' },
                10012: { message: '操作过于频繁，请联系管理员' }
            },
            showCountryCodeList: false,
            identifyingCodeSecond: 0,
            flag: false, // 防重复提交
            isFetching: false,
        };
    },
    methods: {
        clearInput() {
            this.phoneValue = '';
        },
        startSense() {
            if (!this.validatePhone() || this.identifyingCodeSecond > 0) return;
            if (this.isFetching) {
                return;
            }
            this.isFetching = true;
            let is_douyin = getParam('utm_source') === 'toutiao-douyin';
            let origin_gtAppId = '66442f2f720bfc86799932d8ad2eb6c7';
            let douyin_gtAppId = '3fc117af5c1a68bb4711684ac0e9be9a';
            captchaGetSenseKey().then(() => {
                window.LaGouCaptchaGetSenseKey({
                    level: 1,
                    gtAppId: is_douyin ? douyin_gtAppId : origin_gtAppId,
                    tcAppId: '2088474689',
                    successCallback: item => {
                        item.tiktok = is_douyin;
                        this.getCode(item);
                    },
                    closeCallback: item => {
                        this.isFetching = false;
                    },
                    errorCallback: error => {
                        this.isFetching = false;
                        console.log('getSenseKey fail', error);
                    },
                    normalCallback: () => {
                        this.getCode();
                    }
                });
            });
        },
        getCode(senseParam) {
            fetchPhoneCode({
                countryCode: this.countryCode,
                phone: this.phoneValue,
                type: 0,
                ...senseParam
            }).then(res => {
                if (res.state === 1) {
                    this.isFetching = false;
                    this.setTimeOfCode();
                    return;
                }
                this.isFetching = false;
                this.setRequestError(res);
            }).catch(() => {
                this.setErrorTip('获取验证码失败，稍后重试');
                this.isFetching = false;
            });
        },
        selectCountryCode(item) {
            this.countryCode = item;
            this.showCountryCodeList = false;
        },
        showCountryCode() {
            this.showCountryCodeList = true;
        },
        setTimeOfCode() {
            this.identifyingCodeSecond = 60;
            let identifyingCodeTimer = setInterval(() => {
                this.identifyingCodeSecond--;
                if (this.identifyingCodeSecond === 0) {
                    clearInterval(identifyingCodeTimer);
                }
            }, 1000);
        },
        setRequestError(res) {
            let messageTips = this.phoneCodeStateList[res.state] || res;
            this.setErrorTip(messageTips.message);
        },
        validatePhone() {
            let message = '';
            if (this.phoneValue) {
                if (validatePhoneNumber(this.phoneValue)) {
                    return true;
                } else {
                    message = '手机格式不正确，请重新输入';
                }
            } else {
                message = '请输入手机号码';
            }
            this.setErrorTip(message);
            return false;
        },
        validateCode() {
            let message = '';
            if (this.codeValue) {
                if (validateCodeNumber(this.codeValue)) {
                    return true;
                } else {
                    message = '请输入正确的手机验证码';
                }
            } else {
                message = '请输入手机验证码';
            }
            this.setErrorTip(message);
            return false;
        },
        submit() {
            if (this.flag) {
                this.setErrorTip('登陆中，请稍候...');
                return false;
            }
            return new Promise((resolve, reject) => {
                if (!this.validatePhone() || !this.validateCode()) {
                    resolve({
                        state: 2,
                        msg: '校验不通过'
                    });
                } else {
                    let urlParam =  getParam();
                    let submitData = {
                        countryCode: this.countryCode,
                        wxinfo: '',
                        phone: this.phoneValue,
                        phoneVerificationCode: this.codeValue,
                    };
                    if (urlParam.wxinfo) {
                        submitData.wxinfo = urlParam.wxinfo;
                    }
                    this.flag = true;
                    return submitPassWord(submitData).then(res => {
                        if (res.state === 1) {
                            if (this.needRefreshPage) {
                                window.open('/frontLogin.do', '_self');
                                return;
                            } else {
                                this.isLoginSuccess = true;
                                return {
                                    state: 1,
                                    msg: '登陆成功'
                                };
                            }
                        }
                        else {
                            this.flag = false;
                        }
                        this.setRequestError(res);
                        resolve({
                            state: 3,
                            msg: '提交请求失败',
                            content: res
                        });
                    }).catch(res => {
                        this.flag = false;
                        this.setErrorTip('登录失败,请稍后重试');
                        console.log('fetchPhoneCode error', res);
                        resolve({
                                state: 4,
                                msg: '登陆失败'
                        });
                    }).finally(() => {
                        // this.flag = false;
                    });

                }
            });
        },
        closeLoginModal() {
            this.$emit('close');
        },
    },
};